import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { FiArrowLeft } from 'react-icons/fi';

import config from 'config';
import { handleError } from '@mssgme/lib';
import { api, getWorkspace } from '@mssgme/store';
import { Button, Input, Textarea } from '@mssgme/ui';

import styles from './styles.scss';

class BotEdit extends Component {
    static propTypes = {
        card: PropTypes.object.isRequired,
        bot: PropTypes.object.isRequired,
        updateBot: PropTypes.func.isRequired,
        createBot: PropTypes.func.isRequired,
        deleteBot: PropTypes.func.isRequired,
        isNew: PropTypes.bool.isRequired,
        history: PropTypes.object.isRequired,
    };

    state = {
        bot: { ...this.props.bot },
        value: JSON.stringify(this.props.bot.botData, null, 4),
        validJSON: true,
        updatedTimes: 0,
    };

    createBot = async () => {
        const { bot } = this.state;
        const { createBot, history } = this.props;

        try {
            const response = await createBot(bot);
            history.push(`/hiddenbots/${response._id}`);
        } catch (e) {
            this.handleErrors(e);
        }
    };

    updateBot = async () => {
        const { bot, updatedTimes } = this.state;
        const { updateBot } = this.props;

        try {
            await updateBot(bot._id, bot);
            this.setState({ updatedTimes: updatedTimes + 1, errors: null });
        } catch (e) {
            this.handleErrors(e);
        }
    };

    deleteBot = async () => {
        const { bot } = this.state;
        const { deleteBot, history } = this.props;

        try {
            await deleteBot(bot._id);
            history.push('/hiddenbots');
        } catch (e) {
            this.handleErrors(e);
        }
    };

    handleDataChange = ({ target: { value } }) => {
        try {
            this.setState({
                bot: {
                    ...this.state.bot,
                    botData: JSON.parse(value),
                },
                value,
                validJSON: true,
            });
        } catch (e) {
            this.setState({
                value,
                validJSON: false,
            });
        }
    };

    handleChange = ({ target: { name, value } }) => {
        this.setState({
            bot: {
                ...this.state.bot,
                [name]: value,
            },
        });
    };

    handleErrors = (e) => {
        if (e.errors) {
            this.setState({ errors: e.errors });
        } else {
            handleError(e);
        }
    };

    render() {
        const { value, validJSON, bot, slugRequiredError, errors } = this.state;
        const { isNew } = this.props;

        return (
            <div className={styles.root}>
                <div className={styles.half}>
                    <div className={styles.editContainer}>
                        <Link to="/hiddenbots" className={styles.back}>
                            <FiArrowLeft size={16} /> Go back
                        </Link>
                        <Input
                            type="text"
                            value={bot.slug}
                            onChange={this.handleChange}
                            name="slug"
                            hasError={slugRequiredError}
                            className={styles.slugInput}
                        />
                        <Textarea
                            name="botData"
                            maxRows={16}
                            value={value}
                            style={{
                                width: '100%',
                                display: 'block',
                                marginBottom: '20px',
                                border: validJSON ? '' : '1px solid red',
                            }}
                            onChange={this.handleDataChange}
                        />
                        {isNew && (
                            <Button onClick={this.createBot} disabled={!validJSON}>
                                Create bot
                            </Button>
                        )}
                        {!isNew && (
                            <>
                                <Button onClick={this.updateBot} disabled={!validJSON} style={{ marginRight: '20px' }}>
                                    Update
                                </Button>
                                <Button onClick={this.deleteBot} disabled={!validJSON} btnStyle="danger">
                                    Delete
                                </Button>
                            </>
                        )}
                        {errors && <div className={styles.errors}>{JSON.stringify(errors, null, 2)}</div>}
                    </div>
                </div>
                <div className={styles.half}>
                    {this.props.bot.slug && (
                        <iframe
                            className={styles.iframe}
                            src={`${config.main_url}/${this.props.card.profile.url}/bot/${
                                this.props.bot.slug
                            }?updated=${this.state.updatedTimes}`}
                        />
                    )}
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state, { match: { params } }) => {
    const workspace = getWorkspace(state);

    return {
        card: {
            ...workspace.card,
            workspaceId: workspace._id,
            products: {
                ...workspace.card.products,
            },
        },
        bot: workspace.bots.find((bot) => bot._id === params.id) || {
            slug: '',
            botData: {
                startResponseId: 1,
                responses: [{ id: 1, messages: [1] }],
                messages: [{ id: 1, type: 'TEXT', content: 'First bot message' }],
            },
        },
    };
};

const mapDispatchToProps = (dispatch) => ({
    createBot: (payload) => dispatch(api.bots.create({ payload })),
    updateBot: (botId, payload) => dispatch(api.bots.update({ botId, payload })),
    deleteBot: (botId) => dispatch(api.bots.delete({ botId })),
});

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(withTranslation()(BotEdit));
