import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withTranslation } from 'react-i18next';
import { connect } from 'react-redux';
import config from 'config';
import { Link } from 'react-router-dom';
import { FiLink } from 'react-icons/fi';

import { getWorkspace } from '@mssgme/store';
import { LinkButton } from '@mssgme/ui';
import styles from './styles.scss';

class BotsList extends Component {
    static propTypes = {
        bots: PropTypes.array.isRequired,
        card: PropTypes.object.isRequired,
    };

    render() {
        const { bots } = this.props;

        return (
            <div className={styles.root}>
                <ul className={styles.list}>
                    {bots.map((bot) => (
                        <li key={bot._id}>
                            <Link to={`/hiddenbots/${bot._id}`} className={styles.botLink}>
                                <ul className={styles.botMetaList}>
                                    <li>
                                        <div>Bot slug:</div>
                                        <div>{bot.slug}</div>
                                    </li>
                                    <li>
                                        <div>Bot id:</div>
                                        <div>{bot._id}</div>
                                    </li>
                                </ul>
                            </Link>
                            <a
                                href={`${config.main_url}/${this.props.card.profile.url}/bot/${bot.slug}`}
                                target="_blank"
                                rel="noopener noreferrer"
                                className={styles.botLinkExternal}
                            >
                                <FiLink size={30} />
                            </a>
                        </li>
                    ))}
                </ul>
                <LinkButton to="/hiddenbots/new">Create new bot</LinkButton>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    const workspace = getWorkspace(state);

    return {
        bots: workspace.bots,
        card: {
            ...workspace.card,
            workspaceId: workspace._id,
            products: {
                ...workspace.card.products,
                results: state.products.items,
                next: state.products.next,
                publishedCount: state.products.publishedCount,
            },
        },
    };
};

export default connect(mapStateToProps)(withTranslation()(BotsList));
