import React from 'react';
import { useTranslation } from 'react-i18next';

import { auth_telegram_bot, auth_viber_bot } from 'config';

import { ua, generateDeepLink } from '@mssgme/helpers';
import telegramIcon from '@mssgme/app/img/messengers/icon-telegram.svg';
import viberIcon from '@mssgme/app/img/messengers/icon-viber.svg';
import facebookIcon from '@mssgme/app/img/messengers/icon-facebook-login.svg';

import styles from './styles.scss';

const MessengersAuth = () => {
    const { i18n } = useTranslation();

    const ref = `auth_${Date.now()}_${i18n.language}`;

    const telegramUrl = generateDeepLink(
        {
            messenger: 'telegram',
            type: 'public',
            name: auth_telegram_bot,
        },
        { start: ref }
    ).getUrl();

    const viberUrl = generateDeepLink(
        {
            messenger: 'viber',
            type: 'public',
            name: auth_viber_bot,
        },
        { context: ref, text: 'start' }
    ).getUrl();

    const restProps = ua.isMobile ? { target: '_blank', rel: 'noopener noreferrer' } : {};

    return (
        <div className={styles.root}>
            <a className={styles.link} href="/api/auth/login/facebook">
                <img src={facebookIcon} alt="Facebook" />
            </a>
            <a className={styles.link} href={telegramUrl} {...restProps}>
                <img src={telegramIcon} alt="Telegram" />
            </a>
            <a className={styles.link} href={viberUrl} {...restProps}>
                <img src={viberIcon} alt="Viber" />
            </a>
        </div>
    );
};

export default MessengersAuth;
