import React, { useMemo } from 'react';
import { Route, Redirect, Switch, useLocation, useRouteMatch } from 'react-router-dom';
import classNames from 'classnames';

import { useAccessControl } from '@mssgme/hooks';
import SettingsMenu from '../SettingsMenu';
import AccountPage from '../AccountPage';
import { IntegrationsPage } from '@mssgme/inbox';
import TeamPage from '../TeamPage';
import { AboveMobile, Mobile, Scrollbar } from '@mssgme/ui';
import { BillingRouting } from '@mssgme/billing';
import BackButton from '../BackButton';

import styles from './styles.scss';

export default function SettingsPage() {
    const location = useLocation();
    const { isAdmin } = useAccessControl();
    const backButtonPaths = useMemo(
        () => [
            { path: '/s/account/change-password', to: '/s/account', exact: true },
            { path: '/s/account', to: '/s', exact: true, mobile: true },
            { path: '/s/referral', to: '/s', exact: true, mobile: true },
            //{ path: '/s/company', to: '/s', exact: false, mobile: true },
            { path: '/s/integrations', to: '/s', exact: false, mobile: true },
            { path: '/s/team', to: '/s', exact: false, mobile: true },
        ],
        [location]
    );
    const isBilling = useRouteMatch('/s/billing');

    return (
        <>
            <AboveMobile>
                <SettingsMenu />
            </AboveMobile>
            <Scrollbar>
                <div className={classNames(styles.content, { [styles.contentBilling]: isBilling })}>
                    <Mobile>
                        <Switch>
                            {backButtonPaths
                                .filter((i) => i.mobile)
                                .map((item, index) => (
                                    <Route
                                        exact={item.exact}
                                        path={item.path}
                                        key={`bbkey-${index}`}
                                        render={() => (
                                            <div className={styles.back}>
                                                <BackButton to={item.to} />
                                            </div>
                                        )}
                                    />
                                ))}
                        </Switch>
                    </Mobile>
                    <Switch>
                        {backButtonPaths
                            .filter((i) => !i.mobile)
                            .map((item, index) => (
                                <Route
                                    exact={item.exact}
                                    path={item.path}
                                    key={`bbkey-${index}`}
                                    render={() => (
                                        <div className={styles.back}>
                                            <BackButton to={item.to} />
                                        </div>
                                    )}
                                />
                            ))}
                    </Switch>
                    <Switch>
                        <Route path="/s/account" component={AccountPage} />
                        {isAdmin && [
                            <Route key="/s/billing" path="/s/billing">
                                <div>
                                    <BillingRouting />
                                </div>
                            </Route>,
                            <Route key="/s/integrations" path="/s/integrations" component={IntegrationsPage} />,
                            <Route key="/s/team" exact path="/s/team" component={TeamPage} />,
                        ]}
                        <Route
                            exact
                            path="/s"
                            render={() => (
                                <>
                                    <AboveMobile>
                                        <Redirect to="/s/account" />
                                    </AboveMobile>
                                    <Mobile>
                                        <SettingsMenu />
                                    </Mobile>
                                </>
                            )}
                        />
                        <Redirect to="/s" />
                    </Switch>
                </div>
            </Scrollbar>
        </>
    );
}
