import { useState } from 'react';
import { useHistory } from 'react-router';

import { handleError } from '@mssgme/lib';

export default function useSubmit(request, redirectTo) {
    const history = useHistory();
    const [errors, setErrors] = useState(null);
    const [isFetching, setIsFetching] = useState(false);

    const onSubmit = async (e) => {
        e && e.preventDefault();
        setIsFetching(true);

        try {
            await request();
            setIsFetching(false);

            if (redirectTo) history.push(redirectTo);
        } catch (err) {
            if (err.errors) {
                setErrors(err.errors);
            } else {
                handleError(err);
            }

            setIsFetching(false);
        }
    };

    return { errors, isFetching, onSubmit, setErrors, setIsFetching };
}
