import React from 'react';
import { useTranslation } from 'react-i18next';
import { Redirect } from 'react-router-dom';

import { useActions } from '@mssgme/hooks';
import { useEnhancedFormik, SubmitInput, FormField } from '@mssgme/ui';
import styles from './styles.scss';

export default function InviteForm() {
    const { t } = useTranslation();
    const actions = useActions();
    const formik = useEnhancedFormik({
        initialValues: {
            email: '',
            role: 'manager',
        },
        onSubmit: async (payload, { resetForm }) => {
            await actions.api.team.create({ payload });

            resetForm();
        },
    });

    if (formik.errors.payment)
        return (
            <Redirect
                to={{
                    pathname: '/restricted',
                    state: { reason: { code: 'upgradeTeamLimit' }, referrer: '/s/team' },
                }}
            />
        );

    return (
        <form className={styles.root} onSubmit={formik.handleSubmit}>
            <div className={styles.images}>
                <img src={require('../../../../img/man.png')} alt="" />
                <img src={require('../../../../img/woman.png')} alt="" />
            </div>
            <h2 className={styles.heading}>{t('inviteForm.heading')}</h2>
            <div className={styles.description}>{t('inviteForm.invite_text')}</div>

            {/*todo: use formik field*/}
            <FormField
                component={SubmitInput}
                placeholder={t('inviteForm.placeholder')}
                submitLabel={t('inviteForm.button')}
                fetching={formik.isSubmitting}
                errors={formik.errors.email}
                {...formik.getFieldProps('email')}
            />
        </form>
    );
}
