import React from 'react';

import { FormRow, Checkbox, GroupButtons, Button, LinkButton, SelectSimple, FormField } from '@mssgme/ui';
import useAccountForm from './useAccountForm';

import styles from './AccountForm.scss';

export default function AccountForm() {
    const {
        t,
        handleSubmit,
        handleReset,
        handleChange,
        setName,
        errors,
        name,
        email,
        setEmail,
        languageOptions,
        lang,
        setLang,
        subscribe,
        setSubscribe,
        hasChanges,
        isFetching,
    } = useAccountForm();

    return (
        <form onSubmit={handleSubmit} onReset={handleReset}>
            <FormField
                label={t('accountForm.label.name')}
                id="account_name"
                name="name"
                value={name}
                placeholder={t('accountForm.placeholder.name')}
                onChange={handleChange(setName)}
                errors={errors?.name}
            />
            <FormField
                label={t('accountForm.label.email')}
                id="account_email"
                name="email"
                value={email}
                placeholder={t('accountForm.placeholder.email')}
                onChange={handleChange(setEmail)}
                errors={errors?.email}
            />
            <FormField
                label={t('accountForm.label.language')}
                component={SelectSimple}
                options={languageOptions}
                id="account_lang"
                name="lang"
                value={lang}
                onChange={handleChange(setLang)}
                errors={errors?.lang}
            />
            <FormRow>
                <LinkButton to="/s/account/change-password" btnStyle="secondary" outline>
                    {t('accountForm.button.change_password')}
                </LinkButton>
            </FormRow>
            <FormField
                component={Checkbox}
                id="account_subscribe"
                name="subscribe"
                checked={subscribe}
                onChange={handleChange(setSubscribe)}
                placeholder={t('accountForm.placeholder.subscribe')}
            />
            <GroupButtons align="right">
                {hasChanges && (
                    <Button className={styles.button} btnStyle="secondary" outline type="reset">
                        {t('accountForm.button.reset')}
                    </Button>
                )}
                <Button className={styles.button} disabled={!hasChanges || isFetching} type="submit">
                    {t('accountForm.button.save')}
                </Button>
            </GroupButtons>
        </form>
    );
}
