import { useState } from 'react';
import { useTranslation } from 'react-i18next';

import { handleError } from '@mssgme/lib';
import { NOTIFICATIONS_DELAY } from '@mssgme/constants';
import { useActions, useUser } from '@mssgme/hooks';
import { Notification } from '@mssgme/ui';
import { ValidationError } from '@mssgme/api-client';

const languageOptions = [
    {
        label: 'English',
        value: 'en',
    },
    {
        label: 'Русcкий',
        value: 'ru',
    },
];

// TODO: should use formik
export default function useAccountForm() {
    const actions = useActions();
    const { i18n, t } = useTranslation();
    const currentUser = useUser();
    const [email, setEmail] = useState(currentUser.email);
    const [name, setName] = useState(currentUser.name);
    const [subscribe, setSubscribe] = useState(currentUser.subscribe);
    const [lang, setLang] = useState(currentUser.lang);
    const [errors, setErrors] = useState({});
    const [isFetching, setIsFetching] = useState(false);
    const [hasChanges, setHasChanges] = useState(false);

    const handleChange = (setter) => (e) => {
        const target = e.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;

        setter(value);
        setHasChanges(true);
    };

    function handleReset(e) {
        e.preventDefault();

        setEmail(currentUser.email);
        setName(currentUser.name);
        setSubscribe(currentUser.subscribe);
        setLang(currentUser.lang);
        setErrors({});
        setHasChanges(false);
    }

    async function handleSubmit(e) {
        e.preventDefault();

        setIsFetching(true);

        try {
            const result = await actions.api.users.updateMyAccount({ payload: { email, name, subscribe, lang } });
            await i18n.changeLanguage(result.lang);

            setErrors({});
            setHasChanges(false);

            Notification.notify(t('accountForm.success'), {
                showClose: true,
                closeDelay: NOTIFICATIONS_DELAY,
            });
        } catch (err) {
            if (ValidationError.is(err)) {
                setErrors(err.errors);
            } else {
                handleError(err);
            }
        } finally {
            setIsFetching(false);
        }
    }

    return {
        t,
        handleSubmit,
        handleReset,
        handleChange,
        setName,
        errors,
        name,
        email,
        setEmail,
        languageOptions,
        lang,
        setLang,
        subscribe,
        setSubscribe,
        hasChanges,
        isFetching,
    };
}
