import React, { useEffect } from 'react';
import { Redirect, useParams } from 'react-router';
import { useSearchParam } from 'react-use';

import { useActions, useFetching } from '@mssgme/hooks';

export default function LoginByCode() {
    const { code } = useParams();
    const { api } = useActions();
    const redirectTo = useSearchParam('redirect_to');
    const getToken = useFetching(api.auth.getToken);

    useEffect(() => void getToken.run({ code }), []);

    if (getToken.fulfilled) {
        return <Redirect to={redirectTo || '/'} />;
    } else if (getToken.rejected) {
        return <Redirect to="/login" />;
    } else {
        return null;
    }
}
