import { useState } from 'react';
import { useWorkspace, useActions } from '@mssgme/hooks';
import { useSubmit } from '../../hooks';

export const useUrlOnboard = () => {
    const { api } = useActions();
    const workspace = useWorkspace();
    const cardSlug = workspace.card.profile.url;
    const [slug, setSlug] = useState(cardSlug || '');
    const submitState = useSubmit(async () => {
        await api.card.patch({
            payload: { 'profile.url': slug, 'profile.fullname': slug },
        });
        await api.onboarding.update({ payload: { finished: true, lastStep: 'url' } });
    }, '/p');

    return {
        ...submitState,
        slug,
        setSlug,
    };
};
