import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Redirect } from 'react-router';

import { useActions } from '@mssgme/hooks';
import { useEnhancedFormik } from '@mssgme/ui';
import Button from '../../Onboarding/UI/Button';
import FormField from '../../Onboarding/UI/FormField';
import styles from './styles.scss';

export default function RegistrationFormMember({ code, invite }) {
    const { t } = useTranslation();
    const actions = useActions();
    const formik = useEnhancedFormik({
        initialValues: {
            email: invite.email,
            name: '',
            password: '',
        },
        onSubmit: (values) => actions.api.users.join({ payload: { ...values, code } }),
    });

    if (formik.status === 'success') {
        return <Redirect to="/p" />;
    }

    return (
        <form onSubmit={formik.handleSubmit}>
            <FormField
                placeholder={t('label.email')}
                type="email"
                autoComplete="email"
                error={t(formik.errors.email)}
                {...formik.getFieldProps('email')}
            />
            <FormField
                placeholder={t('label.name')}
                type="text"
                autoComplete="name"
                autoFocus
                error={t(formik.errors.name)}
                {...formik.getFieldProps('name')}
            />
            <FormField
                placeholder={t('label.password')}
                type="password"
                autoComplete="new-password"
                error={t(formik.errors.password)}
                {...formik.getFieldProps('password')}
            />
            <div style={{ display: 'flex', justifyContent: 'center', marginTop: 30 }}>
                <Button type="submit" isFetching={formik.isSubmitting}>
                    {t('RegistrationFormMember.button')}
                </Button>
            </div>
            <p className={styles.tip}>
                {t('auth.signup.termsText')}
                <a href="https://mssg.me/terms-of-use" target="_blank" rel="noopener noreferrer">
                    {t('auth.signup.terms')}
                </a>{' '}
                {t('auth.signup.and')}{' '}
                <a href="https://mssg.me/privacy-policy" target="_blank" rel="noopener noreferrer">
                    {t('auth.signup.policy')}
                </a>
            </p>
        </form>
    );
}

RegistrationFormMember.propTypes = {
    code: PropTypes.string.isRequired,
    invite: PropTypes.shape({
        email: PropTypes.string.isRequired,
    }).isRequired,
};
