import React from 'react';
import { useTranslation } from 'react-i18next';
import AuthLayout from '../AuthLayout';
import EnterEmailForm from '../EnterEmailForm';
import styles from './styles.scss';
import Button from '../../Onboarding/UI/Button';

export default function EnterEmailPage() {
    const { t } = useTranslation();

    const handleCancelClick = (e) => {
        e.preventDefault();
        window.location.href = '/logout';
    };

    return (
        <AuthLayout>
            <div className={styles.root}>
                <div className={styles.header}>
                    <h1>{t('EnterEmailPage.heading')}</h1>
                </div>
                <EnterEmailForm />
                <div style={{ display: 'flex', justifyContent: 'center', marginTop: 10 }}>
                    <Button btnStyle="faded" onClick={handleCancelClick}>
                        {t('EnterEmailPage.cancel')}
                    </Button>
                </div>
            </div>
        </AuthLayout>
    );
}
