import { isEmpty } from 'lodash';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';

import { NOTIFICATIONS_DELAY } from '@mssgme/constants';
import { useActions } from '@mssgme/hooks';
import { handleError } from '@mssgme/lib';
import { Notification } from '@mssgme/ui';
import { ValidationError } from '@mssgme/api-client';

export const useChangePasswordForm = () => {
    const actions = useActions();
    const { t } = useTranslation();
    const history = useHistory();

    const [password, setPassword] = useState('');
    const [passwordRepeat, setPasswordRepeat] = useState('');
    const [errors, setErrors] = useState({});
    const [isFetching, setIsFetching] = useState(false);
    const [hasChanges, setHasChanges] = useState(false);

    const disableButton = !hasChanges || isFetching || isEmpty(password) || isEmpty(passwordRepeat);

    const handleChange = (setter) => (e) => {
        const target = e.target;
        const value = target.value;

        setter(value);
        setHasChanges(true);
    };

    async function handleSubmit(e) {
        e.preventDefault();

        if (password !== passwordRepeat) {
            setErrors({ passwordRepeat: t('changePasswordForm.mismatch') });

            return;
        }

        setIsFetching(true);

        try {
            await actions.api.users.changeMyPassword({ payload: { password } });

            history.push('/s/account');

            Notification.notify(t('changePasswordForm.success'), {
                showClose: true,
                closeDelay: NOTIFICATIONS_DELAY,
            });
        } catch (err) {
            if (ValidationError.is(err)) {
                setErrors(err.errors);
            } else {
                handleError(err);
            }

            setIsFetching(false);
        }
    }

    return {
        t,
        handleSubmit,
        password,
        handleChange,
        setPassword,
        errors,
        passwordRepeat,
        setPasswordRepeat,
        disableButton,
    };
};
