import { get } from 'lodash';
import React from 'react';
import { Redirect } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { useActions, useUser } from '@mssgme/hooks';
import { useEnhancedFormik } from '@mssgme/ui';
import Button from '../../Onboarding/UI/Button';
import FormField from '../../Onboarding/UI/FormField';

export default function EnterEmailForm() {
    const { t } = useTranslation();
    const actions = useActions();
    const user = useUser();

    const formik = useEnhancedFormik({
        initialValues: {
            email: get(user, 'email', ''),
            name: get(user, 'name'),
            avatar: get(user, 'avatar'),
            subscribe: get(user, 'subscribe'),
            lang: get(user, 'lang'),
        },
        onSubmit: (payload) => actions.api.users.updateMyAccount({ payload }),
    });

    if (formik.status === 'success') {
        return <Redirect to="/p" />;
    }

    return (
        <form onSubmit={formik.handleSubmit}>
            <FormField
                placeholder={t('label.email')}
                name="email"
                type="email"
                autoComplete="email"
                error={t(formik.errors.email)}
                {...formik.getFieldProps('email')}
            />
            <div style={{ display: 'flex', justifyContent: 'center', marginTop: 40 }}>
                <Button type="submit" isFetching={formik.isSubmitting}>
                    {t('EnterEmailForm.button')}
                </Button>
            </div>
        </form>
    );
}
