import React from 'react';

import { GroupButtons, Button, FormField } from '@mssgme/ui';
import { PasswordWithStrength } from '@mssgme/app/components/UI';
import { useChangePasswordForm } from './useChangePasswordForm';

import styles from './ChangePasswordForm.scss';

export default function ChangePasswordForm() {
    const {
        t,
        handleSubmit,
        password,
        handleChange,
        setPassword,
        errors,
        passwordRepeat,
        setPasswordRepeat,
        disableButton,
    } = useChangePasswordForm();

    return (
        <form onSubmit={handleSubmit}>
            <PasswordWithStrength
                label={t('changePasswordForm.label.new_password')}
                id="new_password"
                name="password"
                lazy
                value={password}
                placeholder={t('changePasswordForm.placeholder.new_password')}
                onChange={handleChange(setPassword)}
                errors={errors.password}
            />
            <FormField
                label={t('changePasswordForm.label.new_password_repeat')}
                id="new_password_repeat"
                name="passwordRepeat"
                type="password"
                value={passwordRepeat}
                placeholder={t('changePasswordForm.placeholder.new_password_repeat')}
                onChange={handleChange(setPasswordRepeat)}
                errors={errors.passwordRepeat}
            />
            <GroupButtons align="right">
                <Button className={styles.button} disabled={disableButton} type="submit">
                    {t('changePasswordForm.button.save')}
                </Button>
            </GroupButtons>
        </form>
    );
}
