import React from 'react';
import { Route, Switch } from 'react-router-dom';
import BotEdit from './BotEdit';
import BotsList from './BotsList';
import styles from './styles.scss';

export default function BotsContainer() {
    return (
        <div className={styles.root}>
            <Switch>
                <Route exact path="/hiddenbots/new" render={(props) => <BotEdit isNew {...props} />} />
                <Route path="/hiddenbots/:id" render={(props) => <BotEdit isNew={false} {...props} />} />
                <Route exact path="/hiddenbots" component={BotsList} />
            </Switch>
        </div>
    );
}
