import React from 'react';
import { Redirect } from 'react-router';
import { useTranslation } from 'react-i18next';

import { useActions } from '@mssgme/hooks';
import { useEnhancedFormik } from '@mssgme/ui';
import { LimitRequestsError } from '@mssgme/api-client';

import Button from '../../Onboarding/UI/Button';
import FormField from '../../Onboarding/UI/FormField';

export default function LoginForm() {
    const { t } = useTranslation();
    const actions = useActions();

    const formik = useEnhancedFormik({
        initialValues: {
            username: '',
            password: '',
        },
        onSubmit: (payload) => actions.api.auth.login({ payload }),
    });

    const limitRequestError = LimitRequestsError.is(formik.submit.error) ? formik.submit.error.message : null;

    if (formik.status === 'success') {
        return <Redirect to="/p" />;
    }

    return (
        <form onSubmit={formik.handleSubmit}>
            <FormField
                placeholder={t('label.email')}
                name="username"
                type="email"
                value={formik.values.username}
                onChange={formik.handleChange}
                autoComplete="email"
                autoFocus
            />
            <FormField
                placeholder={t('label.password')}
                name="password"
                type="password"
                value={formik.values.password}
                onChange={formik.handleChange}
                autoComplete="current-password"
                error={formik.errors.username || limitRequestError}
            />
            <div style={{ display: 'flex', justifyContent: 'center' }}>
                <Button type="submit" isFetching={formik.isSubmitting}>
                    {t('auth.signin.signin')}
                </Button>
            </div>
        </form>
    );
}
