import React from 'react';
import { useTranslation } from 'react-i18next';

import { useActions } from '@mssgme/hooks';
import { useEnhancedFormik } from '@mssgme/ui';
import Button from '../../Onboarding/UI/Button';
import FormField from '../../Onboarding/UI/FormField';
import styles from './styles.scss';

export default function RestoreForm() {
    const { t } = useTranslation();
    const actions = useActions();
    const formik = useEnhancedFormik({
        initialValues: { email: '' },
        onSubmit: (payload) => actions.api.users.recoveryPassword({ payload }),
    });

    return (
        <form onSubmit={formik.handleSubmit}>
            <FormField
                name="email"
                value={formik.values.email}
                onChange={formik.handleChange}
                error={t(formik.errors.email)}
                placeholder={t('auth.restore.placeholder')}
                type="email"
                autoFocus
            />
            {formik.status === 'success' ? (
                <span className={styles.sentText}>{t('auth.restore.success')}</span>
            ) : (
                <div style={{ display: 'flex', justifyContent: 'center' }}>
                    <Button isFetching={formik.isSubmitting} type="submit">
                        {t('auth.restore.button')}
                    </Button>
                </div>
            )}
        </form>
    );
}
