import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import { Button } from '../Buttons';
import styles from './styles.scss';

export default class SubmitInput extends React.Component {
    static propTypes = {
        className: PropTypes.string,
        value: PropTypes.string,
        hasError: PropTypes.bool,
        submitLabel: PropTypes.string,
        disableButton: PropTypes.bool,
        fetching: PropTypes.bool,
    };

    static defaultProps = {
        hasError: false,
        disableButton: false,
        fetching: false,
    };

    render() {
        const { className, hasError, value, disableButton, submitLabel, fetching, ...rest } = this.props;

        return (
            <div className={classNames(styles.root, className)}>
                <input
                    className={classNames(styles.input, { [styles.error]: hasError })}
                    value={value}
                    type="text"
                    {...rest}
                />
                <Button
                    className={styles.button}
                    isFetching={fetching}
                    disabled={!value || value.length === 0 || disableButton}
                    type="submit"
                >
                    {submitLabel}
                </Button>
            </div>
        );
    }
}
