import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Route } from 'react-router-dom';

import styles from './SidebarBlock.scss';

export function SidebarBlock({ className, render, path, exact }) {
    return (
        <Route
            key={path}
            path={path}
            exact={exact}
            render={({ match }) => <div className={classNames(styles.root, className)}>{render && render(match)}</div>}
        />
    );
}

SidebarBlock.propTypes = {
    path: PropTypes.oneOfType([PropTypes.string]).isRequired,
    exact: PropTypes.bool,
    render: PropTypes.func,
    className: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
};
