import PropTypes from 'prop-types';
import React, { Suspense } from 'react';
import { Switch, Router, Route } from 'react-router-dom';
import { Provider } from 'react-redux';

import { useI18n } from '@mssgme/i18n';
import { history } from '@mssgme/services';
import { NotificationsBagLegacy, ModalNotificationProvider, EventEmitterProvider, useUITranslations } from '@mssgme/ui';
import { SplashScreen } from '@mssgme/app/components/UI';
import { useBillingTranslations } from '@mssgme/billing';

import LoginPage from '../Auth/LoginPage';
import RestorePage from '../Auth/RestorePage';
import RegistrationPage from '../Auth/RegistrationPage';
import RegistrationPageMember from '../Auth/RegistrationPageMember';
import LogoutPage from '../Auth/LogoutPage';
import MainPage from '../Common/MainPage';
import PrivateRoute from '../Common/PrivateRoute';
import PublicRoute from '../Common/PublicRoute';
import LoginByCode from '../Auth/LoginByCode';

function LocalesLoader({ children }) {
    useI18n('common', (language) =>
        import(
            /* webpackChunkName: "locales.common" */
            /* webpackInclude: /\.json$/ */
            `../../i18n/locales/${language}/common.json`
        )
    );
    useUITranslations();
    useBillingTranslations();

    return children;
}

App.propTypes = {
    store: PropTypes.object.isRequired,
};

function App({ store }) {
    return (
        <Suspense fallback={<SplashScreen />}>
            <LocalesLoader>
                <Provider store={store}>
                    <Router history={history}>
                        <EventEmitterProvider>
                            <ModalNotificationProvider />
                            <NotificationsBagLegacy />
                            <Switch>
                                <Route exact path="/login/code/:code" component={LoginByCode} />
                                <PublicRoute exact path="/login" component={LoginPage} />
                                <PublicRoute path="/registration" component={RegistrationPage} />
                                <PublicRoute exact path="/join/:code" component={RegistrationPageMember} />
                                <PublicRoute exact path="/restore" component={RestorePage} />
                                <PrivateRoute exact path="/logout" component={LogoutPage} />
                                <PrivateRoute path="/" component={MainPage} />
                            </Switch>
                        </EventEmitterProvider>
                    </Router>
                </Provider>
            </LocalesLoader>
        </Suspense>
    );
}

export default App;
