import React from 'react';
import { get } from 'lodash';
import { useTranslation } from 'react-i18next';
import { MdInfoOutline } from 'react-icons/md';

import Button from '../../UI/Button';
import FormField from '../../UI/FormField';
import HeaderNav from '../../UI/HeaderNav';
import Heading from '../../UI/Heading';
import PageContainer from '../../UI/PageContainer';
import { useUrlOnboard } from './useUrlOnboard';

import styles from './UrlOnboard.scss';

export default function UrlOnboard() {
    const { t } = useTranslation();
    const { isFetching, errors, onSubmit, slug, setSlug } = useUrlOnboard();

    return (
        <PageContainer>
            <div className={styles.root}>
                {/* <HeaderNav backUrl="/onboarding/category" /> */}
                <Heading>{t('Onboarding.urlTitle')}</Heading>
                <form onSubmit={onSubmit}>
                    <FormField
                        placeholder="username"
                        name="slug"
                        value={slug}
                        type="text"
                        autoComplete="userslug"
                        onChange={(e) => setSlug(e.target.value)}
                        error={get(errors, 'profile.url')}
                        preText="mssg.me/"
                        autoFocus
                    />
                </form>
                <div style={{ marginTop: 42 }}>
                    <MdInfoOutline size={42} color="#F2F5FA80" />
                    <p className={styles.tipMessage}>{t('Onboarding.urlTip')}</p>
                </div>
                <div style={{ marginTop: 40, display: 'flex', justifyContent: 'center' }}>
                    <Button onClick={onSubmit} disabled={!slug} isFetching={isFetching}>
                        {t('Onboarding.next')}
                    </Button>
                </div>
            </div>
        </PageContainer>
    );
}
