import React from 'react';
import { Switch, Route, Redirect, useRouteMatch } from 'react-router-dom';

import { useActions, useWorkspace, useUser, useFetch, useAutoSwitchApp } from '@mssgme/hooks';
import { CardPreviewStore, CardPage } from '@mssgme/cards';
import { InboxObserver, InboxPage } from '@mssgme/inbox';
import { SocketObserver, useBreakpoints } from '@mssgme/ui';
import { BillingObserver } from '@mssgme/billing';

import {
    SplashScreen,
    AccountBlocked,
    MainLayout,
    Install,
    CheckUpdate,
    Sidebar,
    InstallPromptProvider,
} from '@mssgme/app/components/UI';

import EnterEmailPage from '../../Auth/EnterEmailPage';
import Onboarding from '../../Onboarding';
import EmptyWorkspace from '../EmptyWorkspace';

import BotsList from '../../ChatBot/Editor/BotsContainer';
import SettingsPage from '../../Settings/SettingsPage';
import HelpPage from '../../Help';
import RestrictedPage from '../RestrictedPage';

export function useIsSidebarVisible() {
    const { isMobile } = useBreakpoints();
    const match = useRouteMatch({
        path: ['/p', '/i', '/s', '/help', '/i/onboard*'],
        exact: true,
    });

    const isRouteMatch = !!match;

    return !isMobile || isRouteMatch;
}

export default function MainPage() {
    const actions = useActions();
    const init = useFetch(actions.api.init);
    const workspace = useWorkspace();
    const user = useUser();
    const isSidebarVisible = useIsSidebarVisible();
    useAutoSwitchApp();

    if (init.error) {
        return <div>Something wrong</div>;
    }

    if (!init.fulfilled) return <SplashScreen />;

    if (user.blocked) {
        return <AccountBlocked isBlockedByIp={user.isBlockedByIp} />;
    }

    if (!user.email) {
        return (
            <Switch>
                <Route exact path="/enter-email" component={EnterEmailPage} />
                <Redirect to="/enter-email" />
            </Switch>
        );
    }

    if (user.workspaces.length === 0) {
        return <EmptyWorkspace />;
    }

    if (!workspace.onboarding.finished) {
        return (
            <Switch>
                <Route path="/onboarding" component={Onboarding} />
                <Redirect to="/onboarding" />
            </Switch>
        );
    }

    return (
        <MainLayout>
            <InstallPromptProvider>
                <CardPreviewStore>
                    <SocketObserver />
                    <BillingObserver />
                    <InboxObserver>
                        <Install />
                        <CheckUpdate />
                        {isSidebarVisible && <Sidebar />}
                        <Switch>
                            <Redirect exact from="/" to="/p" />
                            <Route path="/i">
                                <InboxPage />
                            </Route>
                            <Route path="/p">
                                <CardPage />
                            </Route>
                            <Route path="/s">
                                <SettingsPage />
                            </Route>
                            <Route path="/help">
                                <HelpPage />
                            </Route>
                            <Route path="/hiddenbots">
                                <BotsList />
                            </Route>
                            <Route path="/restricted">
                                <RestrictedPage />
                            </Route>
                        </Switch>
                    </InboxObserver>
                </CardPreviewStore>
            </InstallPromptProvider>
        </MainLayout>
    );
}
