import React from 'react';
import { useRouteMatch } from 'react-router-dom';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { useMount, useUnmount } from 'react-use';

import { useIsNativeApp } from '@mssgme/hooks';
import { Header } from '@mssgme/app/components/UI';
import styles from './styles.scss';

export default function AuthLayout({ children }) {
    const { t } = useTranslation();
    const match = useRouteMatch();
    const isApp = useIsNativeApp();
    const headerButtonTo = ['/login'].includes(match.path) ? '/registration' : '/login';
    const headerButtonText = ['/login'].includes(match.path) ? t('auth.signup.signup') : t('auth.signup.signIn');

    useMount(() => {
        document.documentElement.classList.add(styles.html);
    });

    useUnmount(() => {
        document.documentElement.classList.remove(styles.html);
    });

    return (
        <div className={styles.root}>
            {!isApp && (
                <Header
                    button={{
                        to: headerButtonTo,
                        text: headerButtonText,
                    }}
                />
            )}

            <div className={styles.contentContainer}>
                <div className={styles.content}>{children}</div>
            </div>
        </div>
    );
}

AuthLayout.propTypes = {
    children: PropTypes.any,
};
