import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { Avatar } from '@mssgme/ui';
import RemoveButton from '../RemoveButton';
import styles from './styles.scss';

const MemberItem = ({ item, onDelete }) => {
    const { t } = useTranslation();
    const isInvite = item.kind === 'Invite';
    const handleDeletion = useCallback(
        (e) => {
            e.preventDefault();

            onDelete(item);
        },
        [item]
    );

    return (
        <tr key={item._id} className={cn({ [styles.member]: !isInvite, [styles.invite]: isInvite })}>
            <td>
                {isInvite ? (
                    <Avatar className={styles.avatar} name={item.email} />
                ) : (
                    <Avatar className={styles.avatar} src={item.user.avatarUrl} name={item.user.name} />
                )}
            </td>
            <td>
                {isInvite ? (
                    <>
                        <div className={styles.help}>{t('membersList.invite_sending')}</div>
                        <div className={styles.email}>{item.email}</div>
                    </>
                ) : (
                    <>
                        <div className={styles.name}>{item.user.name}</div>
                        <div className={styles.email}>{item.user.email}</div>
                    </>
                )}
            </td>
            <td>{t(`membersList.roleLabels.${item.role}`)}</td>
            <td>
                {(isInvite || item.role !== 'owner') && (
                    <RemoveButton
                        onClick={handleDeletion}
                        tooltip={t(
                            isInvite ? 'membersList.tooltip_delete_invite' : 'membersList.tooltip_delete_member'
                        )}
                    />
                )}
            </td>
        </tr>
    );
};

MemberItem.propTypes = {
    item: PropTypes.shape({
        _id: PropTypes.string.isRequired,
        kind: PropTypes.string.isRequired,
        role: PropTypes.string.isRequired,
        email: PropTypes.string,
        user: PropTypes.object,
    }).isRequired,
    onDelete: PropTypes.func.isRequired,
};

export default function MemberList({ team, onRemove }) {
    const { t } = useTranslation();

    return (
        <table className={styles.root}>
            <thead>
                <tr>
                    <th />
                    <th>{t('membersList.thead.name')}</th>
                    <th>{t('membersList.thead.permissions')}</th>
                    <th />
                </tr>
            </thead>
            <tbody>
                {team.map((item) => (
                    <MemberItem key={item._id} item={item} onDelete={onRemove} />
                ))}
            </tbody>
        </table>
    );
}

MemberList.propTypes = {
    team: PropTypes.array.isRequired,
    onRemove: PropTypes.func.isRequired,
};
