import React from 'react';
import { useTranslation } from 'react-i18next';
import { useRouteMatch } from 'react-router';

import { useActions, useFetch } from '@mssgme/hooks';
import AuthLayout from '../AuthLayout';
import RegistrationFormMember from '../RegistrationFormMember';
import styles from './styles.scss';

export default function RegistrationPageMember() {
    const actions = useActions();
    const { t } = useTranslation();
    const { params } = useRouteMatch();
    const { pending, result: invite, rejected, getValidationError } = useFetch(
        () => actions.api.users.checkInvite({ payload: { code: params.code } }),
        [params.code]
    );

    if (pending) return null;

    return (
        <AuthLayout>
            <div className={styles.root}>
                <div className={styles.header}>
                    <h1>{t('RegistrationPageMember.heading')}</h1>
                </div>

                {invite && <RegistrationFormMember code={params.code} invite={invite} />}

                {rejected && <p className={styles.wrong}>{t(getValidationError('code'))}</p>}
            </div>
        </AuthLayout>
    );
}
