import React from 'react';
import { Redirect, useLocation } from 'react-router-dom';

import { NoAccess } from '@mssgme/ui';

export default function RestrictedPag() {
    const location = useLocation();
    const { reason, referrer } = location.state || {};

    if (!reason) return <Redirect to="/p" />;

    return <NoAccess center reason={reason} referrer={referrer} />;
}
