import React from 'react';
import { useTranslation } from 'react-i18next';

import { useAccessControl, useExperiments, useBilling } from '@mssgme/hooks';
import { ScrollableBox, Nav, NavItem, NavItemLink, Feature } from '@mssgme/ui';
import { InstallLink } from '@mssgme/app/components/UI';
import TelegramIcon from '@mssgme/app/icons/telegram.svg';
import styles from './styles.scss';

export default function SettingsMenu() {
    const { t } = useTranslation();
    const { isAdmin } = useAccessControl();
    const { isInboxDisplayed } = useExperiments();
    const { isTrial } = useBilling();

    return (
        <div className={styles.root}>
            <ScrollableBox className={styles.scrollBox}>
                <h2 className={styles.heading}>{t('MobileMenu.settings')}</h2>
                <div className={styles.menu}>
                    <Nav>
                        <NavItemLink to="/s/account">{t('settingsMenu.generalMenu.account')}</NavItemLink>
                        {isAdmin && (
                            <NavItemLink to={isTrial ? '/s/billing/plans' : '/s/billing'}>
                                {t('settingsMenu.generalMenu.billing')}
                            </NavItemLink>
                        )}
                        <NavItem tag={InstallLink}>{t('settingsMenu.install')}</NavItem>
                        <NavItemLink className={styles.logout} to="/logout">
                            {t('settingsMenu.logout')}
                        </NavItemLink>
                    </Nav>
                    {isInboxDisplayed && isAdmin && (
                        <Feature rule="inbox">
                            <Nav heading={t('settingsMenu.nav_heading.inbox_settings')}>
                                <NavItemLink to="/s/integrations">
                                    {t('settingsMenu.inboxMenu.integrations')}
                                </NavItemLink>
                                <NavItemLink to="/s/team">{t('settingsMenu.inboxMenu.team')}</NavItemLink>
                            </Nav>
                        </Feature>
                    )}
                    <Nav>
                        <NavItem
                            href="https://mssg.me/r/m/5d63cdd118779000516ccc9b"
                            icon={TelegramIcon}
                            target="_blank"
                            rel="nofollow noindex"
                        >
                            {t('settingsMenu.telegramMenu.label')}
                        </NavItem>
                    </Nav>
                </div>
            </ScrollableBox>
        </div>
    );
}
